@media only screen and (max-width: 880px){

}
.card__container{
    display: flex;
    flex-direction: column;
    background-color: white;
   
}
.image__container{
    width:200px;
    height: 250px;
    
    overflow: hidden;
}
.image__container > img{
    width: 100%;
    height: 100%;
  
}
.product__info{
    position: relative;
}
.product__info >h3{
    text-transform: capitalize;
    color: rgb(228, 29, 228);
    margin: 1% 1%;
}
.product__info > h4 {
    text-transform: capitalize;
    color: rgb(97, 97, 97);
    font-weight: 600;
    padding:1% 0%;
}
.product__info > button{
    position: absolute;
    bottom: 0%;
    right: 0;
    background-color: rgb(247, 190, 67);
    color: white;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-size: 0.9rem;
    font-weight: 600;
    letter-spacing: 2px;
    display: none;
    transition: all .5s ease-in-out;
    cursor: pointer;
}
.card__container:hover .product__info > button{
    display: block;
}
.card__container:focus .product__info > button{
    display: block;
}

